.board-row:after{
  clear:both;
  content: '';
  display: table;
}
.square{
  background: #fff;
  border:1px solid #999;
  text-align: center;
  float:left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0px;
  width: 34px;
}
.status{
  margin-bottom: 10px;
}